import { Link, Switch, Route } from 'react-router-dom';
import * as React from "react";

const Home = () => (
    <div>
        <h1></h1>
        <Link to="/list"></Link>
    </div>
)

const list = [
    'react',
    'koa',
    'ssr'
]

const List = () => (
    <ul>
        { list.map((item, i) => <li key={ i }>{ item }</li>) }
    </ul>
)

export default () => (
    <Switch>
        <Route exact path="/" component={ Home }/>
        <Route exact path="/list" component={ List }/>
    </Switch>
)